import * as React from "react"
import { Button, Container, Grid, Header, Icon, Image, Item, Label, Menu, Segment, Step, Table, } from 'semantic-ui-react'
import Particles from 'react-particles-js';

const pageStyles = {
  
}
const style = {
  h1: {
    marginTop: '1rem',
  },
  Particles:{
  }
}


const IndexPage = () => {
  return (
    <>
      <Header as='h1' content='Bilal Khan' style={style.h1} textAlign='center'/>
      <Container>
        <Segment>Content</Segment>
      </Container>
      <div style={style.Particles}>
        <Particles
          params={{
            particles: {
              color: {
                value: "#f44242"
              },
              number: {
                value: 100
              },
              size: {
                  value: 3
              },
              line_linked: {
                color: '#f44242',
                opacity: 1
              }
            },
            interactivity: {
              events: {
                  onhover: {
                      enable: true,
                      mode: "repulse"
                  }
              }
            }
          }}
        />
      </div>
      
    </>
  )
}

export default IndexPage
